@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0); }

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }

.main-nav__item {
  margin: 0;
  padding: 0; }

.main-nav__link {
  color: #ffffff;
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  padding: 21px 20px 9px;
  display: inline-block;
  opacity: 0.7; }

.page {
  min-width: 320px; }

.page__body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif; }

.page a {
  text-decoration: none; }

.page__header_main {
  background-image: url("/img/promo.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.container {
  width: 873px;
  margin: 0 auto; }
